import React from "react"
import Helmet from "react-helmet"
import { Link, graphql } from "gatsby"
import { rhythm } from "../utils/typography"
import Layout from "../layouts/index.js"

const NoStyleLink = props => (
  <Link css={{ textDecoration: `none` }} {...props} />
)

export default ({ location }) => {
  return (
    <Layout location={location}>
      
      <h1>Deconstructing societal challenges of the 21st century</h1>
      <p>
          <NoStyleLink to="https://tech-stoic.github.io/introduction-v5">Introduction</NoStyleLink><br/><br/>       
          <h4>Part 1: The challenges</h4>
          <NoStyleLink to="https://tech-stoic.github.io/uniquely-human-v3">Chapter 1: The human experience</NoStyleLink><br/>
          <NoStyleLink to="">Chapter 2: The natural world begging our mercy</NoStyleLink><br/>
          <NoStyleLink to="">Chapter 3: Rise of coded technologies</NoStyleLink><br/>
          <NoStyleLink to="">Chapter 4: </NoStyleLink><br/>
          <NoStyleLink to="">Chapter 5:</NoStyleLink><br/>
          <NoStyleLink to="">Chapter 6:</NoStyleLink><br/>
      </p>
    </Layout>
  )
}
